/* 컨테이너 */
.container {margin:0 auto; width: 1100px; height: inherit;}

/* 바디 */
html,body,#root,.cont {width: 100%; height: 100%; color: #fff;}
body {background-color:rgb(70, 70, 70)}

/* 로딩창 */
.loder {width: inherit; height: inherit; display: flex; justify-content: center; align-items: center; background-color: #faf4ff;}
.loder .loder_text {font-weight: bold;}

/* 헤더 */
header {position: relative;}
header .header_top {height: 40px;}
header .header_top .container {border-bottom: 1px solid #D1B2FF;}
header .header_top ul {overflow: hidden; position: absolute; top: 13px; right: 80px; font-size: 14px;}
header .header_top li {float: left; margin-right: 10px; margin-left:10px; color:#fff;}
header .header_top li:hover {text-decoration: underline;}
header .header_center {height: 130px;}
header .header_center h1{text-align: center; line-height: 130px; color: #fff; font-size: 30px;}
