header .linker ul {overflow: hidden; position: absolute; bottom: 5px; right: 100px;}
header .linker li {float: left;}
header .linker li a {padding-right: 20px;} */

/* 네비게이션 - 검색 */
nav .search .container {height: 50px; display:flex; justify-content: center; align-items: center;}
nav .search .search_cont {position: relative;}
nav .search .search_cont input {width:1080px; height: 40px; padding: 1px 8px 1px 8px; border: 3px solid #D1B2FF; background-color: rgb(60,60,60);
    color:#fff; font-size: 15px;}
nav .search .search_cont button {position: absolute; top: 0; right: 0; width: 48px; height: 100%; margin-left: -1px; border: 3px solid #D1B2FF; 
    background: url(../img/search_icon.png) no-repeat #D1B2FF; background-position: 7px 8px; background-size:25px 25px;}
nav .search .search_cont button:active {border-color:#A566FF;}
nav .search .search_cont .search_button {display: inline-block;}

/* 네비게이션 - 메뉴 */
nav .menu {height: 50px;}
nav .menu .container {border-bottom: 1px solid gray;}

/* 컨텐츠 - 추천 */
#content .recommend .container {background-color: rgba(60,60,60,0.8);}
#content .recommend .container .recommend_sort_title {margin-left: 15px;}
#content .recommend .container .movies {margin:0 auto; width: 1030px; padding-top:20px;}
#content .recommend .container .tvshows {margin:0 auto; width: 1030px; padding-top:50px;}

/* 컨텐츠 - 분류 */
#content .category {height: 1000px;}
#content .category .container {background-color: rgba(50,50,50,0.8);}

/* 푸터 */
footer .footer {height: 50px;}
footer .footer .container {background-color: rgba(40,40,40,0.8);}